import { ShoppingBasket, ShoppingCart } from '@material-ui/icons';
import { NavLink } from 'react-router-dom';
import logo from './pics/logo.webp';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../node_modules/bootstrap/dist/js/bootstrap.bundle';

const Navbar = () => {

    return (

        <>

            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <div className="container-fluid">
                    <NavLink className="navbar-brand" to="/">

                        <div className="navbar-logo">
                            <img src={logo} className="logo" />
                            <div className="navbar-heading">
                                <h2>Earthy</h2>
                            </div>
                        </div>

                    </NavLink>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse navbar-items" id="navbarSupportedContent">
                        <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <NavLink className="nav-link active" aria-current="page" to="/">Home</NavLink>
                            </li>

                            <li className="nav-item">
                                <NavLink className="nav-link active" aria-current="page" to="/shop">Shop</NavLink>
                            </li>

                            <li className="nav-item">
                                <NavLink className="nav-link active" aria-current="page" to="/contact">Contact Us</NavLink>
                            </li>

                        </ul>

                    </div>
                </div>
            </nav>

            {/* <div className="navbar">

                <div className="navbar-logo">
                    <img src={logo} className="logo" />
                    <div className="navbar-heading">
                        <h2>Earthy</h2>
                        <p>Sustainable products at affordable prices</p>
                    </div>
                </div>

                <div className="navbar-items">

                    <ul className='navbar-links'>

                        <li>Home</li>

                        <li>Shop</li>

                        <li>Contact Us</li>
                    </ul>

                    <ul>
                        <li><ShoppingCart className='cart-icon' />
                        </li>
                    </ul>
                </div>
            </div> */}

        </>
    )
}

export default Navbar;