import { Add, Remove } from '@material-ui/icons';
import { useState } from 'react';
import loofah from './pics/loofah.webp';

const ItemCard = (props) => {

    const [itemCount, setItemCount] = useState(0);

    return (

        <div className="item">

            <div className="item-description">

            
                <h1>{props.details.name}</h1>
                <p>
                    {props.details.description}
                </p>

                <div className='add-cart'>
                
                <button className='item-btn' onClick={() => {

                    if(itemCount > 0)
                    setItemCount(itemCount-1)
                }}>- </button>

                <span>{itemCount == 0 ? <span className='add-to-cart'> Add To Cart </span> : itemCount}</span> 
                
                <button className='item-btn' onClick={() => {
                    setItemCount(itemCount+1)
                }}>+</button>
                </div>
            </div>

            <div className='item-image' data-aos="flip-left" style={{
                backgroundImage: `url(${props.details.image_url})`,
                // backgroundSize: "cover",
                // height: "100vh",
                
            }}>
                {/* <img src={loofah} height='100%' /> */}
                {/* <img src={props.details.image_url} width='100px' /> */}
            </div>

        </div>
    )
}

export default ItemCard;